import { AudioOutlined } from '@ant-design/icons';
import React from 'react';
import { Input, Row, Col, Image } from 'antd';
import search1 from './images/search1.jpg';
import search2 from './images/search2.jpg';
import search3 from './images/search3.jpg';

const { Search } = Input;
const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: '#1677ff',
    }}
  />
);
const onSearch = (value, _e, info) => console.log(info?.source, value);

const SearchTypo = () => (
  <>
  <div style={{
    width:"200px",
    height:"5vh"
  }}> 
  </div>

  <div style={{display: 'flex', justifyContent: 'center'
              }}>
    <h1>音乐文件搜索引擎</h1>
  </div>

  <div style={{display: 'flex', justifyContent: 'center'
              }}>    
    <Search
      maxLength={100}
      placeholder="输入内容"
      enterButton="搜索"
      size="large"
      suffix={suffix}
      onSearch={onSearch}
    />
  </div>
  <br/>
  <Row>
    <Col span={9} offset={2} style={{display: 'flex', justifyContent: 'right'}}>
      <Image 
      width={"20vh"}
      src={search2}
      />
    </Col>
    <Col span={9} offset={2}>
      <Image
      width={"20vh"}
      src={search1}
      />
    </Col>
    <Col span={9} offset={2} style={{display: 'flex', justifyContent: 'right'}}>
      <Image
      width={"20vh"}
      src={search3}
      />
    </Col>
  </Row>
  </>
);
export default SearchTypo;