import React from 'react';
import { Layout, theme } from 'antd';
import MainMenu from './MainMenu';
import TopHeader from './TopHeader';
import SearchTypo from './Typos/SearchTypo';
import Foot from './Foot';
const { Content, Sider } = Layout;



const Search = () => {
  const {
    token: { colorBgContainer },
  } = theme.useToken();
  return (
    <Layout style={{height:"100vh"}}>
      <TopHeader/>

      <Layout
      style={{background:'#e6f4ff'}}>
        <div
        style={{
          width:190,          
          height:'60vh',
        }}
        ></div>
        <Sider
          breakpoint="sm"
          collapsedWidth="0"
          width={150}
          style={{
            position: 'fixed',
            background: colorBgContainer,
          }}
        >
          <MainMenu/>
        </Sider>

        <Layout
          style={{
            background: '#e6f4ff',
            padding: '0 24px 24px',
          }}
        >
          {/* <Breadcrumb
            style={{
              margin: '16px 0',
            }}
          >
            <Breadcrumb.Item>Home</Breadcrumb.Item>
            <Breadcrumb.Item>List</Breadcrumb.Item>
            <Breadcrumb.Item>App</Breadcrumb.Item>
          </Breadcrumb> */}
          <Content
            style={{
              paddingTop: 40,
              paddingLeft: 0,
              paddingRight: 80,
              marginTop: 40,
              minHeight: 280,
              background: '#e6f4ff',
            }}
          >

            <SearchTypo/>

          </Content>
        </Layout>
      </Layout>

      <Foot/>
    </Layout>
  );
};
export default Search;