import React from 'react';  
import { Divider, Button, Avatar } from 'antd';
import { useNavigate } from "react-router-dom"; 
import myAvatar from './hnuflag.jpg';

const Firstpage = () => {  

    const navigate = useNavigate();  
  
  const toHome = () => {  
    navigate("/Home");  
  }; 
    
  return (    
    <div style={{ height: '100vh', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', backgroundColor:'#e6f4ff' }}>  
         
        
        <Avatar
                        size={{
                        xs: 120,
                        sm: 150,
                        md: 180,
                        lg: 256,
                        xl: 320,
                        xxl: 400,
                        }}
                        src={myAvatar}
                    />    
        
        
        <div style={{ color: '#000000', width: '26%', height: '15vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>  
          <Button type="primary" block  size='large' onClick={toHome}>  
            进入合成器  
          </Button>  
        </div>  
    </div>    
  );    
};  
  
export default Firstpage;  