import React from 'react';
import { Layout } from 'antd';
import logoheader from './image/hnuflag.jpg';

const { Header } = Layout;

const TopHeader = () => {
  
  return (
    <>
      <Header
        style={{
          display: 'flex',
          alignItems: 'center',
          background:'#bae0ff',//拂晓蓝2
        }}
      >
        
        <img src={logoheader} alt="Logo" 
        style={{width:"50px"}}/>
        <h2 style={{ lineHeight: '64px', margin: 0, color: '#364d79' }}>&nbsp;&nbsp;&nbsp;&nbsp;杭 州 师 范 大 学 音 乐 学 院 音 轨 合 成 项 目</h2>  
      </Header>

  </>    
  );
};
export default TopHeader;