import React from 'react';
import { Divider, Typography, Image, FloatButton, Button } from 'antd';
import {BranchesOutlined} from '@ant-design/icons';
import Card1 from '../Card/Card1';
import home1 from './images/home1.jpg';
import { useNavigate } from "react-router-dom"; 
const { Title, Paragraph, Text, Link } = Typography;

const HomeTypo = () => {

   const navigate = useNavigate();  
    const OnClick = () => {  
      navigate('../News');
    }; 

  return(
  <Typography>
    <div style={{display: 'flex', justifyContent: 'center'}}>
    <Image
    width={"80%"}
    src={home1}
  /></div>
    <Title level={1}> 杭州师范大学音乐学院</Title>

    <Paragraph>
    <Text strong>杭州师范大学音乐学院（School of Music Hangzhou Normal University），
    位于浙江省杭州市，截至2019年7月，学院总建筑面积近4万平方米，
    是浙江省最早开办音乐专业本科和研究生教育的教学单位，党总支书记、副院长是冯涯。<br/>
    音乐学院始建于1982年，2014年2月学院成建制划转成立浙江音乐学院，2015年学校重新筹建音乐教育系，
    2017年音乐教育系划归教育学院并恢复招生，2019年3月恢复成立杭州师范大学音乐学院，
    2021年机构改革更名为杭州师范大学音乐学院，知名校友有吕薇等。</Text>
    </Paragraph>
    <Paragraph>
    <Text strong>在2012年教育部第二次学科评估，音乐学学科列为A类学科，排名第32位。</Text>
    </Paragraph>
    <Paragraph>
    <Text strong>音乐学院现拥有浙江省（A类）重点学科、省级重点专业、全国一类特色专业、
    浙江省高校人文社会科学重点研究基地；学院还是全国音乐教育专业的课程改革试点单位。
    据权威机构对全国包括专业音乐学院在内的386个音乐学科的评估，我院连续三年排名全国前十位。</Text>
    </Paragraph>
    <Paragraph>
    <Text strong>学院拥有艺术学一级学科硕士授予权；拥有音乐学、艺术学两个二级学科硕士点和教育硕士、
    艺术硕士两个专业硕士学位点；设有音乐教育、音乐表演、作曲及作曲技术理论、舞蹈学、艺术教育五个本科专业，
    及音乐教育、理论作曲、声乐、钢琴、器乐、舞蹈、流行音系、艺术教育8个教学系，
    南宋乐舞研究中心、张世祥小提琴国际教学研究中心、艺术学研究所、浙派琴筝研习所、音乐教育研究所等5个科研研究机构；
    设有交响乐团、舞蹈团、合唱团、民族乐团；在校全日制本科生635名，硕士研究生81名。</Text>
    </Paragraph>

    <br/>
    <Card1/>
    
  <FloatButton.BackTop />
   
  </Typography>
  );
};
export default HomeTypo;