import React, { useState } from 'react';
import { ExperimentOutlined, PieChartOutlined, FormOutlined, SnippetsOutlined, BranchesOutlined, TagsOutlined, BlockOutlined, ShoppingOutlined, BulbOutlined, TeamOutlined, SubnodeOutlined } from '@ant-design/icons';
import { Menu, Drawer, Typography } from 'antd';
import { useNavigate } from "react-router-dom"; 
const { Title, Paragraph, Text, Link } = Typography;

const items = [
  {  
    key: '1',  
    icon: React.createElement(BlockOutlined),  
    label: '首 页',  
  },
  {
    key: '2',  
    icon: React.createElement(TagsOutlined),  
    label: '登录',  
  },
  {
    key: '3',  
    icon: React.createElement(FormOutlined),  
    label: '搜索',  
  },
  {
    key: '4',  
    icon: React.createElement(PieChartOutlined),  
    label: '音轨合成器',  
  },
  {
    key: '5',  
    icon: React.createElement(TeamOutlined),  
    label: '关于我们',  
  }
]

const MainMenu = () => {

  const [open, setOpen] = useState(false);
  const navigate = useNavigate();  
  const onClose = () => {
    setOpen(false);
  };

  const handleMenuClick = (e) => {  
    const key = e.key;  
    switch (key) {  
      case '1':  
        navigate("/Home");  
        break;  
      case '2':  
        navigate("/login");  
        break;  
      case '3':  
        navigate("/search");  
        break;       
      case '4':  
        navigate("/mix");  
        break;  
      case '5':  
        setOpen(true);
        break;  
      default:  
        navigate("/Home");  
        break;  
    }  
  };  

  return (
    <>

      <Menu
        mode="inline"
        // defaultSelectedKeys={['1']}
        defaultOpenKeys={['3']}
        style={{
          background: '#e6f4ff',
          height: '100%',
          borderRight: 0,
        }}
        items={items}
        onClick={handleMenuClick}
      />
      <Drawer title="关于我们" 
      placement="bottom" 
      height='350px'
      styles={{
        mask: {
        backdropFilter: 'blur(10px)',
      },
      content: {
        boxShadow: '-10px 0 10px #f1f1f1',
      },
      header: {
        borderBottom: `1px solid `,
      },
      body: {
        fontSize: ''
      },
      footer: {
        borderTop: `1px solid `}
      }}

      onClose={onClose} open={open}>
        
        <Typography>
          <Title level={4}>杭州师范大学音乐系临时网站</Title>
          
          <Text ><blockquote>杭州师范大学仓前校区</blockquote></Text>
          <Paragraph>
            小组成员：
          </Paragraph>
          <Paragraph>
            <pre>
              本网站仅作为比赛项目展示
              
            </pre>
          </Paragraph>
          
        </Typography>
      </Drawer>
</>
  );
};
export default MainMenu;