import React from 'react';
import { Layout, theme } from 'antd';
import MainMenu from './MainMenu';
import HomeTypo from './Typos/HomeTypo';
import TopHeader from './TopHeader';
import Foot from './Foot';
const { Content, Sider } = Layout;



const Home = () => {
  const {
    token: { colorBgContainer },
  } = theme.useToken();
  return (
    <Layout>
      <TopHeader/>

      <Layout
        style={{background:'#e6f4ff'}}>
        <div
        style={{
          width:190,          
          height:'60vh',
        }}
        ></div>
        <Sider
          breakpoint="sm"
          collapsedWidth="0"
          width={150}
          height={1000}
          style={{
            position: 'fixed',
            background: '#e6f4ff',
          }}
        >
          <MainMenu/>
        </Sider>

        <Layout
          style={{
            background: '#e6f4ff',
            padding: '0 24px 24px',
          }}
        >
          {/* <Breadcrumb
            style={{
              margin: '16px 0',
            }}
          >
            <Breadcrumb.Item>Home</Breadcrumb.Item>
            <Breadcrumb.Item>List</Breadcrumb.Item>
            <Breadcrumb.Item>App</Breadcrumb.Item>
          </Breadcrumb> */}
          <Content
            style={{
              paddingTop: 40,
              paddingLeft: 20,
              paddingRight: 80,
              paddingBottom: 40,
              marginTop: 24,
              marginBottom: 0,
              minHeight: 280,
              background: '#e6f4ff',
            }}
          >
            <HomeTypo/>
          </Content>
        </Layout>
      </Layout>

      <Foot/>
    </Layout>
  );
};
export default Home;